<template>
  <section-frame :alert='alert'>
    <template #title>
      {{ $t('user.sections.account_settings.password.title') }}
    </template>
    <p class='el:amx-Fs(0.875em) el:amx-Mb(1.5em)'>
      {{ $t('user.sections.account_settings.password.info') }}
    </p>
    <a @click='submit' class='btn btn--blue-flat btn--medium btn--block'>
      {{ $t('user.sections.account_settings.password.change_password_button') }}
    </a>
  </section-frame>
</template>
<script>

  import SectionFrame from '../SectionFrame.vue'

  export default {
    data () {
      return {
        alert: {
          message: '',
          type: ''
        }
      }
    },

    components: {
      SectionFrame
    },

    methods: {
      submit () {
        // if success
        this.alert.message   = this.$i18n.t('user.sections.account_settings.password.success_message', { email: 'johndoe@gmail.com'  })
        this.alert.type      = 'success'
        // if error
        this.alert.message   = this.$i18n.t('user.sections.account_settings.password.error_message', { email: 'johndoe@gmail.com'  })
        this.alert.type      = 'danger'
      }
    }

  }

</script>

