          <ul class='el:m-list el:m-list--hrz el:amx-Fs(0.875em) el:amx-Fw(b) el:amx-Tt(u) el:amx-Mr(2em)@>lg el:amx-D(n)@<sm'>
            <li class='el:amx-Mr(2em)'>
              <a :href="links['home']">{{ $t('dictionary.home') }}</a>
