          <div class='el:o-off-canvas-menu__control' @click="$store.commit('menu/close')"></div>

          <div style='height:32px;'>
            <img v-if="$store.state.profile.avatar != ''" :src='$store.state.profile.avatar' width="32px" class='el:amx-D(ib)' style="border-radius: 50%;vertical-align: top;">
            <img v-else src="./assets/avatar-default.svg" width="32px" class='el:amx-D(ib)' style="border-radius: 50%;vertical-align: top;">
            <div class='el:amx-D(ib) el:amx-Ml(0.5em)'>
              <div class='el:amx-Pos(r) el:amx-Fw(b) el:amx-Fs(1em)' style='top:-2px;'>{{ $t('user.greetings', { name: $store.state.profile.name } ) }}</div>
