                </a>
              </div>
            </template>
            <template #hidden>
              <div v-for='(value, key) in hiddenLocaleOptions' class='el:amx-Mb(1em)'>
                <a @click="$store.commit('locale/set', key)" >
                  <svg width='16px' height='16px' class='el:amx-Mr(0.25em) el:amx-Va(m) el:amx-D(ib)'>
                    <use :xlink:href='`#icons-${key.toLowerCase()}`' />
                  </svg>
