      :content-containers="['container','row','col-12 col-lg-9','el:amx-Pt(1.5em)', 'el:amx-Pb(1.5em)']"
      @onTabClick='changeTab'
      >

      <template #tab1>
        {{ $t('user.tabs.account_settings') }}
      </template>

      <template #tab2>
        {{ $t('user.tabs.interests') }}
      </template>

