      </template>

      <template #tab2-content>
        <router-view name='tab2'></router-view>
      </template>

      <template #tab3-content>
        <router-view name='tab3'></router-view>
      </template>

    </tabs>
  </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex'
import types from './store/mutation-types'
import Header from './components/Header.vue'
import Tabs   from '@classpert/elements/src/vue-components/Tabs.vue'
import Navbar from './components/Navbar.vue'
import OffCanvasMenu from '@classpert/elements/src/vue-components/OffCanvasMenu.vue'
import Collapsible   from '@classpert/elements/src/vue-components/Collapsible.vue'
import ProgressBar   from '@classpert/elements/src/vue-components/ProgressBar.vue'

const { mapActions } = createNamespacedHelpers('profile')

export default {
  name: 'app',

  components: {
    Header,
    Tabs,
    OffCanvasMenu,
    Collapsible,
    Navbar,
    ProgressBar
  },

  data () {
    return {

      webAppURL: '',

      localeOptions: {
        "en":    'hidden',
        "es":    'hidden',
        "pt-BR": 'hidden'
      }

      /*pro: {
        name: 'John Doe',
        email: 'johndoe@classpert.com',
        avatar: '',
        interests: []
      }*/

    }
  },

  watch: {
    '$store.state.locale.code' (val) {
      this.$i18n.locale  = val
      this.webAppURL  = this.$domains[val]
      for (var key in this.localeOptions) {
        this.localeOptions[key] = 'hidden'
      }
      this.localeOptions[val]   = 'visible'
    }
  },

  created () {
    this.$store.commit('locale/set', (this.$route.query.locale || 'en'))
    this.initProfile({user_account_id: this.$session.getUserId()});
  },

  computed: {

    links () {
      return {
        home:       this.webAppURL,
        contactUs:  `${this.webAppURL}/contact-us`,
        blog:       `${this.webAppURL}/blog`
      }
    },

    hiddenLocaleOptions() {
      return _.pickBy(this.localeOptions, (v, k) => {
        return v == 'hidden'
      })
    },

    visibleLocaleOption() {
      return _.pickBy(this.localeOptions, (v, k) => {
        return v == 'visible'
      })
    }

  },

  methods: {
    ...mapActions({
      initProfile: types.PROFILE_GET
    }),
    changeTab (tab, idx, evt) {
      this.$router.push({ name: tab })
    }
  }
}
</script>
