<template>
  <section-frame :alert='alert'>
    <template #title>
      {{ $t('user.sections.account_settings.profile.title') }}
    </template>

    <form class='el:amx-Mt(1.5em)' action='#'>
      <div class="el:m-form-field el:amx-Mb(1em)">
        <div class="el:m-form-field__label el:m-form-field__label--over">
          <label v-html="$t('user.forms.profile.name_field_label')" for="name"></label>
        </div>
        <div class="el:m-form-field__input el:m-form-field__input--medium el:m-form-field__input--block">
          <input name="name" type="text" :placeholder="$t('user.forms.profile.name_field_placeholder')" v-model="name">
        </div>
      </div>

      <div class="el:m-form-field el:amx-Mb(1.625em)">
        <div class="el:m-form-field__label el:m-form-field__label--over">
          <label v-html="$t('user.forms.profile.username_field_label')" for="username"></label>
        </div>
        <div class="el:m-form-field__input el:m-form-field__input--medium el:m-form-field__input--block">
          <input name="username" type="text" :placeholder="$t('user.forms.profile.username_field_placeholder')" v-model="username">
        </div>
      </div>

      <input @click='submit' type='submit' class='btn btn--blue-flat btn--medium btn--block' :value="$t('user.forms.profile.submit')"/>
    </form>
  </section-frame>
</template>

<script>
  import { createNamespacedHelpers } from 'vuex'
  import types from '../../store/mutation-types'
  import SectionFrame from '../SectionFrame.vue'

  const { mapActions, mapMutations } = createNamespacedHelpers('profile')

  export default {
    data () {
      return {
        alert: {
          message: '',
          type: ''
        }
      }
    },

    components: {
      SectionFrame
    },

    computed: {
      name: {
        get() {
          return this.$store.state.profile.name
        },
        set(value) {
          this.change({ name: value })
        }
      },
      username: {
        get() {
          return this.$store.state.profile.username
        },
        set(value) {
          this.change({ username: value })
        }
      }
    },

    methods: {
      ...mapMutations({ change: types.PROFILE_UPDATE }),
      ...mapActions({ persist: types.PROFILE_UPDATE }),
      submit (event) {
        event.preventDefault()
        this.persist({
          user_account_id: this.$session.getUserId()
        })
      }
    }
  }
</script>

