<template>
  <div>
    <file-uploader
      :multiple='false'
      :axios-config="{method: 'put', headers: {'x-amz-acl': 'private'}}"
      :auto='false'
      :label="$t('user.components.certificate_uploader.file_label')"
      :max-allowed-size="maxAllowedSize"
      accept="image/jpeg,image/gif,image/png,application/pdf"
      @onFilesEnqueued='onFilesEnqueued'
      @onUploadProgress='onUploadProgress'
      @onUploadError='onUploadError'
      @onUploadSuccess='onUploadSuccess'
      >

      <template slot-scope="{ uploadList }">
        <ul class='c-list c-list--unstyled el:amx-Mt(0.375em)'>
          <li v-for='item in uploadList' :key='item.file.name' style='margin-bottom:8px'>
            <span class='el:amx-Fs(0.75em)' v-if='item.valid'>
              {{ item.file.name }}
            </span>
            <span class='el:amx-Fs(0.75em)' v-else>
              {{ item.feedback }}
            </span>
            <div class='el:amx-Mt(0.25em)'>
              <progress-bar :progress='item.progress.value' :feedback='item.progress.feedback' :state='item.progress.state'>
                <div class='el:amx-D(ib)' style='width:80%'>{{ item.progress.feedback }}</div><div
                  class='el:amx-D(ib) el:amx-Ta(r)' style='width:20%'>{{ item.progress.value }}%</div>
              </progress-bar>
            </div>
          </li>
        </ul>
      </template>
    </file-uploader>
  </div>
</template>
<script>
  import { createNamespacedHelpers } from 'vuex'
  import types from '../store/mutation-types'
  import FileUploader from '@classpert/elements/src/vue-components/FileUploader.vue'
  import ProgressBar from '@classpert/elements/src/vue-components/ProgressBar.vue'

  const { mapActions: mapCertificateActions } = createNamespacedHelpers('certificate')
  const { mapMutations: mapPromoMutations } = createNamespacedHelpers('promo_account')

  export default {
    data () {
      return {
        maxAllowedSize: {
          value: '20MB',
          feedback: (item, ctx) => {
            return this.$t('user.components.certificate_uploader.file_size_feedback', {
              file: item.file.name,
              size: '20MB'
            })
          }
        }
      }
    },

    components: {
      FileUploader,
      ProgressBar
    },

    methods: {
      ...mapCertificateActions({
        authCertificate: types.CERTIFICATE_AUTH
      }),
      ...mapPromoMutations({
        updatePromoCertificate: types.PROMO_ACCOUNT_CREATE
      }),
      onFilesEnqueued (uploadList, uploadHandler) {
        const cert = uploadList[0]
        this.authCertificate({
          user_account_id: this.$session.getUserId(),
          file_name: encodeURI(cert.file.name)
        }).then(({ upload_url, file_content_type }) => {
          return uploadHandler(cert, {
            url: upload_url,
            headers: {
              'Content-type': file_content_type
            }
          })
        })
      },

      onUploadProgress(item, pEvt) {
        item.progress.feedback = this.$t('user.components.certificate_uploader.progress_feedback.loading')
      },

      onUploadError(item, resp) {
        item.progress.feedback = this.$t('user.components.certificate_uploader.progress_feedback.error')
      },

      onUploadSuccess(item, resp) {
        item.progress.feedback = this.$t('user.components.certificate_uploader.progress_feedback.success')
        this.updatePromoCertificate({file: item.file.name})
        this.$emit('success')
      }
    }
  }
</script>
