import env from './environment'
import jwt from './jwt'
import domains from './domains'
const axios = require('axios')

// Log in | Log out paths
const logInPath = '/user_accounts/sign_in'
const logOutPath = '/user_accounts/sign_out'

export default {

  logInURL () {
    return domains['en'] + logInPath
  },

  logOutURL () {
    return domains['en'] + logOutPath
  },

  logIn (nonce) {
    return axios.post(env.tokenAuthURL + '/' + encodeURIComponent(nonce)).then((resp) => {
      jwt.set(resp.data.token)
    })
  },

  getApiAuthHeader () {
    return `Bearer ${jwt.get()}`
  },

  getUserId () {
    return jwt.decode().sub
  },

  isLoggedIn () {
    try {
      var cJWT  = jwt.decode(),
      cTime     = new Date().getTime() / 1000
      if (cTime > cJWT.exp) {
        this.logOut()
        return false
      }
      return true
    } catch(err) {
      return false
    }
  },

  requireSignIn (redirPath='/') {
    let url = `${this.logInURL()}?redir=${env.appURL}${redirPath}`
    location.replace(url)
  },

  logOut () {
    axios.delete(this.logOutURL()).then((resp) => {
      jwt.remove()
    })
  }


}
