<template>
  <section-frame :alert='alert'>
    <template #title>
      {{ $t('user.side_menus.promo.get_a_10_bonus') }}
    </template>
    <validation-observer v-slot="{ invalid, validate, untouched }" slim>
      <form @submit.prevent="validate().then(submit)" class='el:amx-Mt(1.5em)' action='#'>
        <validation-provider #default="{ errors }" tag="div" name='price' rules="required">
          <div class="el:m-form-field el:amx-Mb(1em)">
            <div class="el:m-form-field__label el:m-form-field__label--over">
              <label for="price">
                {{ $t('user.forms.promo.price_field_label') }}
              </label>
            </div>

            <div class="el:m-form-field__input el:m-form-field__input--medium el:m-form-field__input--block">
              <money name="price" v-model="price" v-bind='money'></money>
              <svg v-if='errors.length > 0' class='el:m-form-field__input-icon el:amx-C_red2' width="1.125em" height="1.125em" viewbox='0 0 24 24'>
                <use xlink:href='#icons-wrong' />
              </svg>
              <svg v-if='price != null && errors.length == 0' class='el:m-form-field__input-icon el:amx-C_blue3' width="1.125em" height="1.125em" viewbox='0 0 24 24'>
                <use xlink:href='#icons-checked' />
              </svg>
              <div v-if='errors.length > 0' class='el:amx-Mt(0.25em) el:amx-C_red2'>
                <span class='el:amx-Fs(0.75em)'>{{ errors[0] }}</span>
              </div>
            </div>
          </div>
        </validation-provider>

        <validation-provider #default="{ errors }" tag="div" name='purchase_date' rules="required">
          <div class="el:m-form-field el:amx-Mb(1em)">
            <div class="el:m-form-field__label el:m-form-field__label--over">
              <label for="purchase_date">
                {{ $t('user.forms.promo.purchase_date_field_label') }}
              </label>
            </div>
            <div class="el:m-form-field__input el:m-form-field__input--medium el:m-form-field__input--block">
              <v-date-picker :max-date='new Date()' :select-attribute='selectAttribute' is-inline name="purchase_date" type="text" :from-page='datePickerFromPage' v-model='purchase_date' ></v-date-picker>
              <svg v-if='errors.length > 0' class='el:m-form-field__input-icon el:amx-C_red2' width="1.125em" height="1.125em" viewbox='0 0 24 24'>
                <use xlink:href='#icons-wrong' />
              </svg>
              <svg v-if='purchase_date && errors.length == 0' class='el:m-form-field__input-icon el:amx-C_blue3' width="1.125em" height="1.125em" viewbox='0 0 24 24'>
                <use xlink:href='#icons-checked' />
              </svg>
              <div v-if='errors.length > 0' class='el:amx-Mt(0.25em) el:amx-C_red2'>
                <span class='el:amx-Fs(0.75em)'>{{ errors[0] }}</span>
              </div>
            </div>
          </div>
        </validation-provider>

        <validation-provider #default="{ errors }" tag="div" name='order_id' rules="required">
          <div class="el:m-form-field el:amx-Mb(1em)">
            <div class="el:m-form-field__label el:m-form-field__label--over">
              <label for="order_id">
                {{ $t('user.forms.promo.order_id_field_label') }}
              </label>
            </div>
            <div class="el:m-form-field__input el:m-form-field__input--medium el:m-form-field__input--block">
              <input name="order_id" type="text" :placeholder="$t('user.forms.promo.order_id_field_placeholder')" v-model='order_id'>
              <svg v-if='errors.length > 0' class='el:m-form-field__input-icon el:amx-C_red2' width="1.125em" height="1.125em" viewbox='0 0 24 24'>
                <use xlink:href='#icons-wrong' />
              </svg>
              <svg v-if='order_id && errors.length == 0' class='el:m-form-field__input-icon el:amx-C_blue3' width="1.125em" height="1.125em" viewbox='0 0 24 24'>
                <use xlink:href='#icons-checked' />
              </svg>
              <div v-if='errors.length > 0' class='el:amx-Mt(0.25em) el:amx-C_red2'>
                <span class='el:amx-Fs(0.75em)'>{{ errors[0] }}</span>
              </div>
            </div>
          </div>
        </validation-provider>

        <div class='el:amx-D(f) el:amx-FxJc(sb)'>
          <input type='button' style='visibility:hidden'/>
          <input :disabled="invalid || sending" @click.prevent="$parent.$emit('next')" type='submit' class='btn btn--blue-flat btn--medium' :value="$t('user.next')"/>
        </div>
      </form>
    </validation-observer>
  </section-frame>
</template>

<script>
  import { createNamespacedHelpers } from 'vuex'
  import types from '../../store/mutation-types'
  import SectionFrame from '../SectionFrame.vue'

  const { mapActions: mapPromoActions, mapMutations: mapPromoMutations } = createNamespacedHelpers('promo_account')

  export default {
    data () {
      return {
        sending: false,
        alert: {
          message: '',
          type: ''
        },
        selectAttribute: {
          highlight: {
            class: 'vc-day-content-custom',
            contentClass: 'vc-day-content-custom'
          }
        }
      }
    },

    components: {
      SectionFrame
    },

    computed: {
      // basic models
      price: {
        get() {
          return this.$store.state.promo_account.price || 0
        },
        set(value) {
          this.changePromo({ price: value })
        }
      },
      purchase_date: {
        get() {
          const date = this.$store.state.promo_account.purchase_date || (new Date()).toISOString().split('T')[0]

          return new Date(`${date} 00:00:00`)
        },
        set(value) {
          if(value) {
            this.changePromo({
              purchase_date: value.toISOString().split('T')[0]
            })
          } else {
            this.changePromo({
              purchase_date: null
            })
          }
        }
      },
      order_id: {
        get() {
          return this.$store.state.promo_account.order_id
        },
        set(value) {
          this.changePromo({ order_id: value })
        }
      },
      money() {
        return {
          decimal: this.$store.state.locale.decimalSeparator,
          thousands: this.$store.state.locale.thousandSeparator,
          prefix: '$ ',
          suffix: '',
          precision: 2,
          masked: false
        }
      },
      datePickerFromPage() {
        const date = this.purchase_date

        return {
          month: date.getMonth() + 1,
          year: date.getFullYear()
        }
      }
    },

    methods: {
      ...mapPromoMutations({
        changePromo: types.PROMO_ACCOUNT_CREATE
      })
    }
  }
</script>

<style lang="scss">
@import '@classpert/elements/src/scss/config/variables/_colors';

.vc-day-content-custom {
  &, &:focus, &:hover {
    background-color: map-get($el-colors, 'blue3') !important;
  }
}
</style>