const $ = require('deepmerge')

// en
const enPublic  = require('json-loader!yaml-loader!../locales/en/en.yml')
const enUser    = require('json-loader!yaml-loader!../locales/en/en.user.yml')
const enTags    = require('json-loader!yaml-loader!../locales/en/en.tags.yml')
const en        = $.all([enUser, enTags, enPublic])

// es
const esPublic  = require('json-loader!yaml-loader!../locales/es/es.yml')
const esUser    = require('json-loader!yaml-loader!../locales/es/es.user.yml')
const esTags    = require('json-loader!yaml-loader!../locales/es/es.tags.yml')
const es        = $.all([esUser, esTags, esPublic])

// pt-BR
const ptBRPublic  = require('json-loader!yaml-loader!../locales/pt-BR/pt-BR.yml')
const ptBRUser    = require('json-loader!yaml-loader!../locales/pt-BR/pt-BR.user.yml')
const ptBRTags    = require('json-loader!yaml-loader!../locales/pt-BR/pt-BR.tags.yml')
const ptBR        = $.all([ptBRUser, ptBRTags, ptBRPublic])

const all = $.all([en, es, ptBR])

export default all
