import { pgrestApi as api } from '../../config/api'
import types from '../mutation-types'

const state = {
  name: "",
  avatar: "",
  username: "",
  interests: []
}

const mutations = {
  [types.PROFILE_GET] (state, payload) {
    state.name = payload.name || state.name
    state.username = payload.username || state.username
    state.interests = payload.interests || state.interests
    state.avatar = payload.avatar || state.avatar
  },
  [types.PROFILE_UPDATE] (state, payload) {
    for (const key in payload) {
      if (payload.hasOwnProperty(key)) {
        state[key] = payload[key]
      }
    }
  }
}

const actions = {
  [types.PROFILE_GET] ({ commit }, { user_account_id }) {
    return new Promise((resolve, reject) => {
      api.get(`/profiles?user_account_id=eq.${user_account_id}`).then((response) => {
        commit(types.PROFILE_GET, response.data[0])
        resolve(response)
      }, (error) => {
        reject(error)
      })
    })
  },
  [types.PROFILE_UPDATE] ({ commit, state }, { user_account_id }) {
    return new Promise((resolve, reject) => {
      api.patch(`/profiles?user_account_id=eq.${user_account_id}`, state).then((response) => {
        commit(types.PROFILE_UPDATE, state)
        resolve(response)
      }, (error) => {
        reject(error)
      })
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
