<template>
  <div v-if='!$store.state.progress_bar.loading'>
    <wizard :num-of-steps=3 v-if='needsPromoInformation'>
      <template #wizard-item-1-content>
        <promo-form></promo-form>
      </template>

      <template #wizard-item-2-content>
        <certificate-form></certificate-form>
      </template>

      <template #wizard-item-3-content>
        <paypal-form></paypal-form>
      </template>
    </wizard>
    <side-menu :num-of-items=3 v-else>
      <template #side-menu-item-1>
        <span class='el:m-label'>
          <icon name='coin' class='el:m-label__icon el:amx-Mr(0.75em)'></icon>
          <span class='el:m-label__text el:amx-Fs(0.875em)'>
            {{ $t('user.side_menus.promo.get_a_10_bonus') }}
          </span>
        </span>
      </template>

      <template #side-menu-item-2>
        <span class='el:m-label'>
          <icon name='certificate' class='el:m-label__icon el:amx-Mr(0.75em)'></icon>
          <span class='el:m-label__text el:amx-Fs(0.875em)'>
            {{ $t('user.side_menus.promo.certificate') }}
          </span>
        </span>
      </template>

      <template #side-menu-item-3>
        <span class='el:m-label'>
          <icon name='paypal' class='el:m-label__icon el:amx-Mr(0.75em)'></icon>
          <span class='el:m-label__text el:amx-Fs(0.875em)'>
            {{ $t('user.side_menus.promo.paypal_account') }}
          </span>
        </span>
      </template>

      <template #side-menu-item-1-content>
        <promo-info></promo-info>
        <help class='el:amx-Mt(1em) el:amx-Fs(0.75em) el:amx-D(n)@<sm'/>
      </template>

      <template #side-menu-item-2-content>
        <certificate-info class='el:amx-Mt(1.5em)@<sm'></certificate-info>
        <help class='el:amx-Mt(1em) el:amx-Fs(0.75em) el:amx-D(n)@<sm'/>
      </template>

      <template #side-menu-item-3-content>
        <paypal-info class='el:amx-Mt(1.5em)@<sm'></paypal-info>
        <help class='el:amx-Mt(1em) el:amx-Fs(0.75em)'/>
      </template>

    </side-menu>
  </div>
</template>

<script>
  import { createNamespacedHelpers } from 'vuex'
  import types from '../store/mutation-types'
  import Wizard from './Wizard.vue'
  import PromoForm from './promo/PromoForm.vue'
  import CertificateForm from './promo/CertificateForm.vue'
  import PaypalForm from './promo/PaypalForm.vue'
  import SideMenu from './SideMenu.vue'
  import PromoInfo from './promo/PromoInfo.vue'
  import CertificateInfo from './promo/CertificateInfo.vue'
  import PaypalInfo from './promo/PaypalInfo.vue'
  import Help from './Help.vue'

  const { mapActions: mapPromoActions, mapMutations: mapPromoMutations } = createNamespacedHelpers('promo_account')
  const { mapActions: mapCertificateActions, mapMutations: mapCertificateMutations } = createNamespacedHelpers('certificate')

  export default {
    data() {
      return {
        needsPromoInformation: true
      }
    },

    components: {
      Wizard,
      PromoForm,
      CertificateForm,
      PaypalForm,
      SideMenu,
      PromoInfo,
      CertificateInfo,
      PaypalInfo,
      Help
    },

    beforeMount() {
      this.$store.commit('progress_bar/toggle', true);
    },

    created() {
      this.initPromo({
        id: this.$uuid(),
        user_account_id: this.$session.getUserId()
      }).then(
        () => {
          if(this.$store.state.promo_account.certificate_id) {
            return this.initCertificate({
              id: this.$store.state.promo_account.certificate_id
            })
          } else {
            return this.initCertificate({
              id: this.$uuid()
            })
          }
        }
      ).then(
        () => {
          if(this.$store.state.promo_account.order_id) {
            this.needsPromoInformation = false
          }
          this.$store.commit('progress_bar/toggle', false);
        }
      )
    },

    methods: {
      ...mapPromoActions({
        initPromo: types.PROMO_ACCOUNT_GET
      }),
      ...mapCertificateActions({
        initCertificate: types.CERTIFICATE_GET,
      })
    }
  }

</script>
