<template>
  <section-frame :alert='alert'>
    <div>
      <div class='el:amx-Fs(0.75em)'>{{ $t('user.sections.promo.paypal_account.title') }}</div>
      <div class='el:amx-Fw(b)'>{{ paypal_account }}</div>
    </div>
    <template v-if="state =='pending' || state == 'rejected'">
      <div class='el:amx-Mt(1.25em) el:amx-Bob_gray2'></div>
      <div class='el:amx-Mt(1.125em)'>
        <div class='el:amx-Fs(0.75em)'>
          {{ $t('user.sections.promo.paypal_account.change.call_out') }}
          <a class='el:amx-C_blue3 el:amx-Fw(b)' href='#' @click.prevent='showModal = true'>
            {{ $t('user.sections.promo.paypal_account.change.link') }}
          </a>
        </div>
      </div>
      <modal name='certificate'
            v-if="showModal"
            @close="closeModal"
            :wrapperClasses="['col-12 col-md-6']">
        <template #body>
          <validation-observer v-slot="{ invalid, validate, untouched }" slim>
            <form @submit.prevent="validate().then(submit)" class='el:amx-Mt(1.5em)' action='#'>
              <validation-provider #default="{ errors: clientSideErrors }" tag="div" name='paypal_account' rules="required">
                <div class="el:m-form-field el:amx-Mb(1em)" :class='[(clientSideErrors.length > 0 || serverSideErrors.length > 0) && "el:m-form-field--error", sending && "el:m-form-field--disabled"]'>
                  <div class="el:m-form-field__label el:m-form-field__label--over">
                    <label for="paypal_account">
                      {{ $t('user.forms.paypal_account.paypal_email_field_label') }}
                    </label>
                  </div>
                  <div class="el:m-form-field__input el:m-form-field__input--medium el:m-form-field__input--block">
                    <input name="paypal_account" type="text" :placeholder="$t('user.forms.paypal_account.paypal_email_field_placeholder')" v-model.lazy='paypal_account' @focus='serverSideErrors = []'>
                    <svg v-if='clientSideErrors.length > 0 || serverSideErrors.length > 0' class='el:m-form-field__input-icon el:amx-C_red2' width="1.125em" height="1.125em" viewbox='0 0 24 24'>
                      <use xlink:href='#icons-wrong' />
                    </svg>
                    <svg v-if='paypal_account && clientSideErrors.length == 0 && serverSideErrors.length == 0' class='el:m-form-field__input-icon el:amx-C_blue3' width="1.125em" height="1.125em" viewbox='0 0 24 24'>
                      <use xlink:href='#icons-checked' />
                    </svg>
                    <div v-if='clientSideErrors.length > 0 || serverSideErrors.length > 0' class='el:amx-Mt(0.25em) el:amx-C_red2'>
                      <span class='el:amx-Fs(0.75em)'>{{ clientSideErrors[0] || serverSideErrors[0] }}</span>
                    </div>
                  </div>
                </div>
              </validation-provider>

              <div class='el:amx-D(f) el:amx-FxJc(sb)'>
                <input :disabled="invalid || sending" @click.prevent='submit' type='submit' class='btn btn--blue-flat btn--medium' :value="submitText"/>
              </div>
            </form>
          </validation-observer>
        </template>
      </modal>
    </template>
  </section-frame>
</template>

<script>
import { createNamespacedHelpers, mapState } from 'vuex'
import types from '../../store/mutation-types'
import SectionFrame from '../SectionFrame.vue'
import Modal from '@classpert/elements/src/vue-components/Modal.vue'

const { mapActions: mapPromoActions, mapMutations: mapPromoMutations } = createNamespacedHelpers('promo_account')
const { mapState: mapPromoState } = createNamespacedHelpers('promo_account')

export default {
  data() {
    return {
      showModal: false,
      sending: false,
      oldPaypalAccount: null,
      serverSideErrors: [],
      alert: {
        message: '',
        type: ''
      }
    }
  },
  components: {
    SectionFrame,
    Modal
  },
  computed: {
    paypal_account: {
      get() {
        return this.$store.state.promo_account.paypal_account
      },
      set(value) {
        this.oldPaypalAccount = this.paypal_account
        return this.changePromo({ paypal_account: value })
      }
    },
    ...mapPromoState({
      state: state => state.state
    }),
    submitText() {
      if(this.sending) {
        return this.$t('user.submitting')
      } else {
        return this.$t('user.submit')
      }
    }
  },
  methods: {
    ...mapPromoMutations({
      changePromo: types.PROMO_ACCOUNT_CREATE
    }),
    ...mapPromoActions({
      persistPromo: types.PROMO_ACCOUNT_CREATE
    }),
    closeModal() {
      if(this.serverSideErrors.length > 0) {
        this.paypal_account = this.oldPaypalAccount
        this.serverSideErrors = []
        this.sending = false
      }
      this.showModal = false
    },
    submit (event) {
      event.preventDefault()
      this.sending = true
      this.serverSideErrors = []

      this.persistPromo({
        user_account_id: this.$session.getUserId(),
        certificate_id: this.$store.state.certificate.id
      }).then(
        () => {
          this.showModal = false
          this.sending = false
        },
        ({ response }) => {
          this.sending = false

          if (response && response.data && response.data.details === 'error') {
            this.serverSideErrors.push(this.$t('user.forms.paypal_account.paypal_email_field_invalid_feedback_text'))
          } else {
            this.serverSideErrors.push(this.$t('user.generic_error'))
          }
        }
      )
    }
  }
}
</script>
