import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

// Import Session
import session from '../config/session'

// Components
import Home from '../Home.vue'
import AccountSettings from '../components/AccountSettings.vue'
import Promo from '../components/Promo.vue'
import Interests from '../components/Interests.vue'

const routes = [
  {
    path: '/',
    name: 'root',
    component: Home,
    children:[
      {
        path: '/account-settings',
        name: 'tab1',
        components: {
          tab1: AccountSettings
        }
      },
      {
        path: '/interests',
        name: 'tab2',
        components: {
          tab2: Interests
        }
      },
      {
        path: '/get-a-$10-bonus',
        name: 'tab3',
        components: {
          tab3: Promo
        }
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

// Authenticate and request a JWT
router.beforeEach((to, from, next) => {
  let nonceToken = to.query.token
  if (nonceToken) {
    session.logIn(nonceToken).then(() => {
      if (session.isLoggedIn()) {
        next()
      } else {
        session.requireSignIn()
      }
    })
  } else {
    if (session.isLoggedIn()) {
      next()
    } else {
      session.requireSignIn()
    }
  }
})

router.afterEach((to, from) => {
  // clean token
  if (to.query.token) {
    window.location.replace(`${to.path}?locale=${to.query.locale}`)
  }
})

export default router
