<template>
  <section-frame :alert='alert'>
    <template #title>
      {{ $t('user.side_menus.promo.paypal_account') }}
    </template>
    <validation-observer v-slot="{ invalid, validate, untouched }" slim>
      <form @submit.prevent="validate().then(submit)" class='el:amx-Mt(1.5em)' action='#'>
        <validation-provider #default="{ errors }" tag="div" name='paypal_account' rules="required">
          <div class="el:m-form-field el:amx-Mb(1em)">
            <div class="el:m-form-field__label el:m-form-field__label--over">
              <label for="paypal_account">
                {{ $t('user.forms.paypal_account.paypal_email_field_label') }}
              </label>
            </div>
            <div class="el:m-form-field__input el:m-form-field__input--medium el:m-form-field__input--block">
              <input name="paypal_account" type="text" :placeholder="$t('user.forms.paypal_account.paypal_email_field_placeholder')" v-model.lazy='paypal_account'>
              <svg v-if='errors.length > 0' class='el:m-form-field__input-icon el:amx-C_red2' width="1.125em" height="1.125em" viewbox='0 0 24 24'>
                <use xlink:href='#icons-wrong' />
              </svg>
              <svg v-if='paypal_account && errors.length == 0' class='el:m-form-field__input-icon el:amx-C_blue3' width="1.125em" height="1.125em" viewbox='0 0 24 24'>
                <use xlink:href='#icons-checked' />
              </svg>
              <div v-if='errors.length > 0' class='el:amx-Mt(0.25em) el:amx-C_red2'>
                <span class='el:amx-Fs(0.75em)'>{{ errors[0] }}</span>
              </div>
            </div>
          </div>
        </validation-provider>

        <div class='el:amx-D(f) el:amx-FxJc(sb)'>
          <input @click.prevent="$parent.$emit('previous')" type='button' class='btn btn--blue-flat btn--medium' :value="$t('user.previous')"/>
          <input :disabled="invalid || sending" @click.prevent='submit' type='submit' class='btn btn--blue-flat btn--medium' :value="submitText"/>
        </div>
      </form>
    </validation-observer>

    <modal name='paypal'
           v-if="showModal"
           @close="closeModal"
           :wrapperClasses="['col-12 col-md-6']">
      <template #header>
        <h3>{{ $t('user.modals.promo.title') }}</h3>
      </template>
      <template #body>
        <p v-html="$t('user.modals.promo.body')"></p>
      </template>
      <template #footer>
        <button type='button' @click.prevent='$router.go()' class='btn btn--blue-flat btn--medium'>
          {{ $t('user.modals.promo.button') }}
        </button>
      </template>
    </modal>
  </section-frame>
</template>

<script>
  import { createNamespacedHelpers } from 'vuex'
  import types from '../../store/mutation-types'
  import SectionFrame from '../SectionFrame.vue'
  import Modal from '@classpert/elements/src/vue-components/Modal.vue'

  const { mapActions: mapPromoActions, mapMutations: mapPromoMutations } = createNamespacedHelpers('promo_account')

  export default {
    data () {
      return {
        sending: false,
        submitted: false,
        showModal: false,
        oldPaypalAccount: null,
        alert: {
          message: '',
          type: ''
        }
      }
    },

    components: {
      SectionFrame,
      Modal
    },

    computed: {
      paypal_account: {
        get() {
          return this.$store.state.promo_account.paypal_account
        },
        set(value) {
          this.oldPaypalAccount = this.paypal_account
          return this.changePromo({ paypal_account: value })
        }
      },
      submitText() {
        if(this.sending) {
          return this.$t('user.submitting')
        } else {
          return this.$t('user.submit')
        }
      }
    },

    methods: {
      ...mapPromoMutations({
        changePromo: types.PROMO_ACCOUNT_CREATE
      }),
      ...mapPromoActions({
        persistPromo: types.PROMO_ACCOUNT_CREATE
      }),
      submit (event) {
        this.sending = true
        event.preventDefault()
        this.persistPromo({
          user_account_id: this.$session.getUserId(),
          certificate_id: this.$store.state.certificate.id
        }).then(
          () => {
            this.showModal = true
            this.sending = false
            this.submitted = true
          },
          (error) => {
            console.log(error)
          }
        )
      },
      closeModal(event) {
        if(this.submitted) {
          this.$router.go()
        } else {
          this.showModal = false
        }
      }
    }
  }

</script>


