<template>
  <section-frame :alert='alert'>
    <div>
      <div class='el:amx-Fs(0.75em)'>{{ $t('user.sections.promo.get_a_10_bonus.order_id') }}</div>
      <div class='el:amx-Fw(b)'>{{ orderId }}</div>
    </div>
    <div class='el:amx-D(ib) el:amx-Mt(1.5em) el:amx-Mr(3.5em)'>
      <div class='el:amx-Fs(0.75em)'>{{ $t('user.sections.promo.get_a_10_bonus.price') }}</div>
      <div class='el:amx-Fw(b)'>{{ price == 0 ? $t('user.free') : price }}</div>
    </div>
    <div class='el:amx-D(ib)'>
      <div class='el:amx-Fs(0.75em)'>{{ $t('user.sections.promo.get_a_10_bonus.purchase_date') }}</div>
      <div class='el:amx-Fw(b)'>{{ purchaseDate }}</div>
    </div>
    <div class='el:amx-Mt(2em) el:amx-Bob_gray2'></div>
    <div class='el:amx-Mt(1.5em)'>
      <div class='el:amx-Fs(0.75em)'>{{ $t('user.sections.promo.get_a_10_bonus.bonus_status') }}</div>
      <div :class="['el:amx-Mt(0.5em)','el:amx-Fw(b)',certificateStatusClass]">
        {{ $t(`user.sections.promo.get_a_10_bonus.bonus_statuses.${this.state || 'default'}`) }}
      </div>
      <div v-if="state == 'pending' || state == 'locked'" class='el:amx-Fs(0.75em) el:amx-Mt(0.5em)'>
        {{ $t('user.sections.promo.get_a_10_bonus.bonus_statuses_additional.pending') }}
      </div>
      <div v-if="state == 'rejected'" class='el:amx-Fs(0.75em) el:amx-Mt(0.5em)'>
        {{ $t('user.sections.promo.get_a_10_bonus.bonus_statuses_additional.failed') }}
      </div>
    </div>
  </section-frame>
</template>

<script>
import { createNamespacedHelpers, mapState } from 'vuex'
import types from '../../store/mutation-types'
import SectionFrame from '../SectionFrame.vue'

const { mapState: mapPromoState } = createNamespacedHelpers('promo_account')

export default {
  data() {
    return {
      alert: {
        message: '',
        type: ''
      }
    }
  },
  components: {
    SectionFrame
  },
  computed: {
    ...mapPromoState({
      orderId: state => state.order_id,
      state: state => state.state,
      price(state) {
        return state.price && `$ ${Number(state.price.toFixed(2)).toLocaleString(this.$store.state.locale.code)}`;
      },
      purchaseDate: state => (new Date(state.purchase_date)).toISOString().split('T')[0]
    }),
    certificateStatusClass() {
      switch (this.state) {
        case 'pending':
        case 'locked':
          return 'el:amx-C_yellow4';
        case 'accepted':
        case 'approved':
          return 'el:amx-C_green4';
        case 'rejected':
          return 'el:amx-C_red4';
        default:
          return 'el:amx-C_yellow4';
      }
    }
  }
}
</script>