<template>
  <section-frame :alert='alert'>
    <div>
      <div class='el:amx-Fs(0.75em)'>{{ $t('user.sections.promo.certificate.title') }}</div>
      <div class='el:amx-Fw(b)'>{{ decodeURI(file) }}</div>
    </div>
    <template v-if="state == 'pending' || state == 'rejected'">
      <div class='el:amx-Mt(1.25em) el:amx-Bob_gray2'></div>
      <div class='el:amx-Mt(1.125em)'>
        <div class='el:amx-Fs(0.75em)'>
          {{ $t('user.sections.promo.certificate.change.call_out') }}
          <a class='el:amx-C_blue3 el:amx-Fw(b)' href='#' @click.prevent='showModal = true'>
            {{ $t('user.sections.promo.certificate.change.link') }}
          </a>
        </div>
      </div>
    </template>
    <modal name='certificate'
           v-if="showModal"
           @close="showModal = false"
           :wrapperClasses="['col-12 col-md-6']">
      <template #body>
        <div class="el:m-form-field el:amx-Mb(1em)">
          <div class="el:m-form-field__label el:m-form-field__label--over">
            <label for="certificate_file">
              {{ $t('user.forms.certificate.certificate_field_label') }}
            </label>
          </div>
          <certificate-uploader @success='onCertificateUploadSuccess'></certificate-uploader>
        </div>
      </template>
    </modal>
  </section-frame>
</template>

<script>
import { createNamespacedHelpers, mapState } from 'vuex'
import types from '../../store/mutation-types'
import SectionFrame from '../SectionFrame.vue'
import CertificateUploader from '../CertificateUploader.vue'
import Modal from '@classpert/elements/src/vue-components/Modal.vue'

const { mapState: mapPromoState } = createNamespacedHelpers('promo_account')

export default {
  data() {
    return {
      showModal: false,
      done: false,
      alert: {
        message: '',
        type: ''
      }
    }
  },
  components: {
    SectionFrame,
    CertificateUploader,
    Modal
  },
  computed: {
    ...mapPromoState({
      state: state => state.state,
      file: state => state.file
    }),
  },
  methods: {
    onCertificateUploadSuccess() {
      this.done = true
      this.showModal = false
    }
  }
}
</script>
