// Vue
import Vue from 'vue'
// Vue Libs
import VueI18n from 'vue-i18n'
import Multiselect from 'vue-multiselect'
import VCalendar from 'v-calendar'
import money from 'v-money'
import uuidv4 from 'uuid/v4'

import { extend, install as VeeValidate, ValidationProvider, ValidationObserver, Validator } from 'vee-validate'
import { required, email, min } from 'vee-validate/dist/rules'
import enValidations from 'vee-validate/dist/locale/en'
import esValidations from 'vee-validate/dist/locale/es'
import ptBrValidations from 'vee-validate/dist/locale/pt_BR'

// Import router
import router from './router'

// Import Vuex Store instance
import store from './store'

// Import Locales
import translations from './config/locales'

// Import Root Tags (Deprecated)
import rootTags from './config/root_tags'

// Import Session and JWT
import session from './config/session'

// Import Domains
import appDomains from './config/domains'

// Import API clients
import { pgrestApi, railsApi } from './config/api'

// Global Components
import App from './App.vue'
import Icon from './components/Icon.vue'
Vue.component('icon', Icon)
Vue.component('multiselect', Multiselect)
Vue.component('ValidationProvider', ValidationProvider)
Vue.component('ValidationObserver', ValidationObserver)

// Hook plugins
Vue.use(VueI18n)
const i18n = new VueI18n({
  locale: 'en',
  messages: translations
})

// Set auth header for api clients
pgrestApi.defaults.headers.common['Authorization'] = session.getApiAuthHeader()
railsApi.defaults.headers.common['Authorization']  = session.getApiAuthHeader()

Vue.use(VeeValidate, {
  i18nRootKey: 'validations',
  i18n,
  dictionary: {
    'en': enValidations,
    'es': esValidations,
    'pt-BR': ptBrValidations
  }
})

// Add validation rules
extend('required', required)
extend('email', email)
extend('min', min)
extend('mismatch', {
  params: [{name: 'other', isTarget: true}],
  validate: (value, { other }) => value === other,
  message: (_, values) => i18n.t(`validations.${values._rule_}`, values)
})

Vue.use(money, {precision: 2})
Vue.use(VCalendar)

Vue.prototype.$api      = pgrestApi
Vue.prototype.$railsApi = railsApi
Vue.prototype.$domains  = appDomains
Vue.prototype.$rootTags = rootTags
Vue.prototype.$session  = session
Vue.prototype.$uuid     = uuidv4

new Vue({
  i18n,
  router,
  store,
  render: h => h(App)
}).$mount('#app')
