import { pgrestApi as api } from '../../config/api'
import types from '../mutation-types'

const state = {
  id: null,
  price: 0.00,
  purchase_date: null,
  order_id: null,
  paypal_account: null,
  certificate_id: null,
  state: 'initial',
  file: null
}

const mutations = {
  [types.PROMO_ACCOUNT_GET] (state, payload) {
    for (const key in payload) {
      if (payload.hasOwnProperty(key)) {
        state[key] = payload[key]
      }
    }
  },
  [types.PROMO_ACCOUNT_CREATE] (state, payload) {
    for (const key in payload) {
      if (payload.hasOwnProperty(key)) {
        state[key] = payload[key]
      }
    }
  }
}

const actions = {
  [types.PROMO_ACCOUNT_GET] ({ commit }, { user_account_id, id }) {
    return new Promise((resolve, reject) => {
      api.get(`/promo_accounts?user_account_id=eq.${user_account_id}`,{
        headers: { Prefer: "return=representation" }
      }).then((response) => {
        commit(types.PROMO_ACCOUNT_GET, response.data[0] || { id: id })
        resolve(response)
      }, (error) => {
        reject(error)
      })
    })
  },
  [types.PROMO_ACCOUNT_CREATE] ({ commit, state }, { user_account_id, certificate_id }) {
    return new Promise((resolve, reject) => {
      api.post(`/promo_accounts`, {
        id: state.id,
        user_account_id: user_account_id,
        price: state.price,
        purchase_date: state.purchase_date,
        order_id: state.order_id,
        paypal_account: state.paypal_account
      }, {
        headers: {
          "Prefer": "return=representation",
          "CertificateId": certificate_id
        }
      }).then((response) => {
        commit(types.PROMO_ACCOUNT_CREATE, response.data[0])
        resolve(response)
      }, (error) => {
        reject(error)
      })
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}